.strudygo-inner {
  grid-template-rows: 1fr;
  grid-template-columns: 20rem 12rem 8fr;
  align-items: center;
  display: grid;
  overflow: hidden;
}

.strudygo-timeline-outer {
  flex-direction: column;
  align-self: flex-end;
  align-items: center;
  display: flex;
}

.strudygo-timeline-circle-outer {
  aspect-ratio: 1;
  opacity: 0;
  border-radius: 3rem;
  width: 6rem;
  display: grid;
}

.strudygo-timeline-circle-inner {
  background-color: var(--main-bg-color);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin: 4px;
  display: flex;
}

.strudygo-timeline-circle-inner > span {
  color: #fff;
  font-size: 2em;
  font-weight: 700;
}

.strudygo-timeline-line {
  opacity: 0;
  width: 4px;
  height: 75vh;
}

.strudygo-info-outer {
  grid-template-rows: 1fr 2fr 2fr;
  grid-template-columns: 6fr 6fr;
  align-self: flex-end;
  height: calc(75vh + 5.5rem);
  display: grid;
}

.strudygo-info-title {
  color: #fff;
  font-size: 4em;
  font-weight: 900;
  transform: translate(200%);
}

.strudygo-info-description {
  color: var(--gray-text-color);
  font-size: 2em;
  transform: translate(200%);
}

.strudygo-stack-outer {
  transform: translate(200%);
}

.strudygo-stack-title {
  color: #fff;
  font-size: 3em;
  font-weight: 600;
}

.strudygo-stack-description {
  color: #fff;
  font-size: 2em;
}

